
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import rankList from 'components/rankList/index'
import http from 'js/http'

const baseList = [{
url: 'javascript:',
img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vvsr72j20p00gogo2.jpg',
title: '送你一朵fua'
}, {
url: 'javascript:',
img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vw1k2wj20p00goq7n.jpg',
title: '送你一辆车'
}]
export default {
name: "home",
components: {
Swiper, SwiperItem, Tab, TabItem,
rankList
},
data() {
return {
swiperList: baseList,
nodeTab: [
'超级节点',
'高级节点'
],
currentTab: 0,
currentNodeId: '',
nodeList: []
}
},
methods: {
jjj(id) {
id = 1
let aaa = '333'
this.$router.push({
path: `/home/notice-details/${aaa}`
})
},
selectTab(index) {
this.currentTab = index
},
getNoticeList() {
http.post('/app/notice', {}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.swiperList = res.content
})
},
getNodeTab() {
http.post('/node', {}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.nodeTab = res.content
this.currentNodeId = res.content[0].id
this.getNodeList()
})
},
getNodeList() {
http.post('/node/vote', {
id: this.currentNodeId
}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.nodeList = res.content


})
}

},
created() {
this.getNoticeList()
this.getNodeTab()
}
}



